@import url('https://fonts.googleapis.com/css2?family=Lateef&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap');

.page-bottom {
    display: flex;
    margin-top: 5rem;
    position: relative;
    padding: 0 7rem 8rem 3rem;
}

.bottom-left {
    flex-grow: 1;
    max-width: 50%;
    display: flex;
    flex-direction: column;
}

.bottom-right {
    flex-grow: 5;
    display: flex;
    justify-content: flex-end;
}

.bottom-logo {
    width: 11rem;
}

.bottom-img-container {
    display: flex;
    justify-content: center;
}

.bottom-reasons {
    display: flex;
    justify-content: space-evenly;
    align-items: flex-end;
}

.img-container {
    width: 6rem;
    text-transform: uppercase;
    font-size: 0.8rem;
    font-family: 'Lateef', cursive;
    color: #735C74;
    display: flex;
    flex-direction: column;
    align-items: center;
    white-space: nowrap;
}

.bottom-text {
    font-family: 'Architects Daughter', cursive;
    text-transform: uppercase;
    color: #EF696E;
    text-align: center;
    font-size: 2rem;
    padding: 0 3rem;
    margin-top: 2rem;
    max-width: 50%;
}

.gray-text {
    color: #272334;
}

.img-masks {
    height: 100%;
}

.mask-container {
    position: relative;
    height: 25rem;
}

.masks-text {
    position: absolute;
    top: 50%;
    transform: translateY(-20%);
    left: 6rem;
    z-index: 2;
    color: #EF696E;
    font-size: 3rem;
    font-family: 'Architects Daughter', cursive;
}

.gray-bg {
    background-color: rgba(61, 83, 68, 0.31);
    position: absolute;
    width: 100%;
    height: 18.5rem;
    bottom: 0;
    left: 0;
    z-index: -1;
}

.bottom-left-container {
    display: flex;
    justify-content: center;
}